.form {
  background-color: aqua;
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 80px;
  font-size: 50px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  /* box-shadow: -2px -2px 2px 2px gray; */
  border-radius: 34px;
  padding: 17px;
}

.form-input {
  padding: 10px;
  border: 0;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
  border-radius: 10px;
  font-size: 20px;
  margin-right: 10px;
}

.form-button {
    border-radius: 25px;
    width: 134px;
    height: 50px;
    font-size: 34px;
    background-color: red;
    text-align: center;
    color: wheat; 
}

.form-button:hover {
    cursor: pointer;
}

.char-info {
    /* display: flex; */
    width: 1000px;
    height: 1000px;
    position: fixed;
    top: 40%;
    left: 24%;
    color: rebeccapurple; 
    /* transform: translate(-50%, -50%); */
}

* {
    text-align: center;
}

body {
    background-color: violet;
}